import React, {
    Component
} from 'react';
//import ReactDOM from 'react-dom';
import './Login.css';
import ajax from '../ajax.js';
import {
    Redirect
} from 'react-router-dom';
import Guide from '../Guide/Guide.js';

class Login extends Component {
    constructor() {
        super();

        this.allOrz = [{
            name: "",
            statement: ['', '', 'Web', '', '']
        }, {
            name: "",
            statement: ['']
        }, {
            name: "",
            statement: ['']
        }, {
            name: "",
            statement: ['']
        }, {
            name: "",
            statement: ['', '', '', '', '', '', '', '']
        }, {
            name: "",
            statement: ['', '', '', '', '', '']
        }, {
            name: "",
            statement: ['', '', '', '']
        }, {
            name: "",
            statement: ['', '', '', '', '', '']
        }, {
            name: "",
            statement: ['', '', '', '']
        }, {
            name: "",
            statement: ['', '', '', '', '', '', '', '', '']
        }, {
            name: "",
            statement: ['', '', '']
        }];
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.getChecked = this.getChecked.bind(this);
        this.showGuide = this.showGuide.bind(this);
        this.back = this.back.bind(this);
        this.state = {
            oname: '',
            password: '',
            logined: false,
            checked: false,
            guideBook: false
        }
    }

    handleSubmit() {
        let that = this;
        //console.log(this.state);
        ajax({
            url: 'https://bmtest.redrock.team/469bba0a564235dfceede42db14f17b0/login',
            method: 'POST',
            data: {
                username: this.state.oname,
                passwd: this.state.password.toString()
            },
            header: 'application/x-www-form-urlencoded',
            headers: {
                'Content-type': 'application/x-www-form-urlencoded'
            },
            success: (res) => {
                let headerData = res.getAllResponseHeaders();
                console.log(JSON.parse(res.response));
                if (JSON.parse(res.response).name === this.state.oname) {

                    for (var i = 0; i < this.allOrz.length; i++) {
                        if (this.allOrz[i].name === this.state.oname) {
                            localStorage.setItem('orz', JSON.stringify(this.allOrz[i]));
                            break;
                        }
                    }

                    //console.log(headerData.split('\n')[0].split(': ')[1]);
                    localStorage.setItem('authorization', headerData.split('\n')[0].split(': ')[1])
                    localStorage.setItem('oname', that.state.oname);
                    alert('Login successfully');
                    that.setState({
                        logined: true
                    })
                } else {
                    alert('Login failed');
                }
            }
        })
        return false;
    }

    handleChange(e) {
        if (e.target.type === 'text') {
            this.setState({
                oname: e.target.value
            })
        } else if (e.target.type === 'password') {
            this.setState({
                password: e.target.value
            })
        }
    }
    getChecked(e) {
        this.setState({
            checked: e.target.checked
        })
    }
    showGuide() {
        this.setState({
            guideBook: true
        })
    }
    back() {
        this.setState({
            guideBook: false
        })
    }
    render() {
        if (this.state.logined) {
            return <Redirect to="/index"/>
        }
        if (this.state.guideBook) {
            return <Guide back={this.back} />
        }
        return (
            <div id="login">
                <div className="title"></div>
                <input type="text" placeholder="" value={this.state.oname} onChange={this.handleChange} />
                <input type="password" placeholder="" value={this.state.password} onChange={this.handleChange} />
                <div className="check">
                    <input type="checkbox" onChange={this.getChecked}/><span onClick={this.showGuide}></span>
                </div>
                <input disabled={!this.state.checked} type="submit" value="Login" onClick={this.handleSubmit} />
            </div>
        )
    }
}



export default Login;


