import React, {
	Component
} from 'react';


class Guide extends Component {
	render() {
		return (
			<div className="guide">
    			<div className="content"></div>
    			<button className="back" onClick={this.props.back} ></button>
    		</div>
		)
	}
}

export default Guide;


